.sideMenu {
    height: 270px;
    width: 60px;
    position: absolute;
    top: 23%;
    left: -25px;
    background-color: rgb(36, 36, 36);
    border-radius: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: right;
    opacity: 0.8;
    transition: 0.2s;
    z-index: 1;
    &:hover {
      background-color: rgb(55, 55, 55);  
  }
}

.sideMenu img {
    width: 25px;
    margin-right: 5px;
}

.sideMenu-expanded {
    height: 270px;
    position: absolute;
    top: 23%;    
    border-radius: 20px;
    transition: 0.2s;
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: 1;
}

.hide {
    width: 35px;
    height: 100%;
    background-color: rgb(36, 36, 36);
    opacity: 0.8;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:hover {
        background-color: rgb(55, 55, 55);  
    }
}

.hide img{
    width: 25px;
}

.options {
    height: 100%;
    width: 180px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(36, 36, 36);
    opacity: 0.8;
}

.item {
    height: 50px;
    width: 150px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    line-height: 60px;
    display: flex;
    align-items: center;
    &:hover {
        background-color: rgb(55, 55, 55);
    }
    &:active {
      transform: scale(0.9);
    }
}

.item img {
    height: 20px;
    margin: 0 3px;
}

.item p{
    margin: 0;
}

.extra-containers {
    z-index: 1;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 50vw;
    height: auto;
    padding-bottom: 10px;
    display: inline;
    background-color: black;
    border-radius: 50px;
    border: 6px solid #26A69A;
    font-weight: 100;
  }
  .closeButton {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    height: 40px;
    margin-bottom: 10px;
    cursor: pointer;  
    &:hover {
      opacity: 0.7;
    }
    &:active {
      height: 55px;
      width: 55px;
    }
  }
  #support-content {
    font-size: 25px;
    text-align: center;
    color: white;
    margin: 0px 20px;
    margin-bottom: 10px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    line-height: 1.5em;
  }
  
  .donateImages {
    text-align: center; /* Centrar horizontalmente el contenido */
    padding: 10px 0px;
    cursor: pointer;
  }
  .paypal {
    height: auto;
    width: 300px;
    display: block; /* Asegura que las imágenes sean elementos de bloque */
    margin: 0 auto;
    &:hover {
      opacity: 0.7;
    }
    &:active {
      transform: scale(0.95);
    }
  }
  .buyme {
    width: 250px;
    height: 60px;
    margin-top: 25px;
    &:hover {
      opacity: 0.7;
    }
    &:active {
      transform: scale(0.95);
    }
  }

.principles-content ul {
    padding: 0px;
    margin: 0px;
  }
.principles-content li {
  text-align: justify;
  margin-bottom: 20px;
  margin-right: 40px;
  margin-left: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  line-height: 1.5em;
  color: white;
  list-style:circle;
  font-size: 22px;
}

.extra-containers p {
    font-size: 20px;
    text-align: center;
    font-weight: bold;
    color: white;
    margin: 10px 40px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    line-height: 1.3em;
}

@media (max-width: 870px) {
  .sideMenu {
    top: 16%;
  }
  .sideMenu-expanded {
    top: 16%;
  }

}

@media (max-width: 670px) {
  .sideMenu {
    width: 40px;
    height: 200px;
    top: 5px;
  }
  .sideMenu img {
    width: 15px;
    margin-right: 0px;
  }
  .sideMenu-expanded {
    top: 5px;
    height: 200px
  }
  .hide {
    width: 30px
  }
  .hide img {
    width: 15px;
  }
  .options {
    width: 120px;
    justify-content: space-evenly;
    opacity: 1;
  }
  .item {
    width: 110px;
    height: 30px;
    font-size: 12px;
    background-color: rgb(55, 55, 55)
  }
  .item img {
    width: 12px;
    height: auto;
  }
  .extra-containers {
    width: 80vw;
  }
  .extra-containers p{
    font-size: 12px;
    font-weight: 300;
  }
  .closeButton {
    height: 30px;
  }
  #support-content {
    font-size: 14px;
    font-weight: 100;
  }
  .paypal{
    height: 30px;
    width: 120px;
  }
  .buyme {
    height: 30px;
    width: 110px;
    margin-top: 15px;
  }
  .principles-content li {
    margin-bottom: 20px;
    margin-right: 10px;
    margin-left: 0px;
    line-height: 1.2em;
    text-align: left;
    font-size: 14px;
  }

}


