  
 .workingspace {
 } 
  .upperMenu {
    height: 50px;
    display: flex;
    align-items:center;
    justify-content: space-around;
    padding: 10px 0px;
  }

  .select {
    height: 40px;
    padding: 5px;
    border: 2px solid #025D8A; 
    border-radius: 10px;
    background-color: #fff; 
    color: #333; 
    font-size: 14px; 
    font-weight: bold;
    outline: none; 
    cursor: pointer;
  }
.select:focus {
   border: 1px solid #007bff; 
   box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); 
  }

.select option {
  font-size: 15px;
  color: black;
 }

  #searchField {
    height: 40px;
    width: 130px;
    font-weight: 600;
    border:2px solid #025D8A;
    background: url('../imagenes/lupa.png') 5px no-repeat;
    background-color: white;
    border-radius: 10px;
    background-size: auto 55%; /* Ajusta el tamaño de la imagen verticalmente centrada */
    padding-left: 25px;
    text-indent: 10px;
    font-size: 13px;
    outline: none;
  }
  #searchField:focus {
    border: 2px solid #007bff; /* Cambia el borde al enfocar el elemento */
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Agrega una sombra al enfocar el elemento */
  }

#classicsButton, #backButton {
  height: 45px;
  width: 150px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  color: black;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  outline: none; 
  border:2px solid #26A69A;
    &:active {
    transform: scale(0.95);
  }
  &:hover {
    background-color: rgb(210, 215, 215);
  }
}
#classicsButton:focus {
  border: 2px solid #007bff; /* Cambia el borde al enfocar el elemento */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* Agrega una sombra al enfocar el elemento */
}




  @media (max-width: 670px) {
    .upperMenu{
      height: auto;
      display: flex;
      flex-direction: column;
      padding: 10px 0px;
    }
    .select{
      margin-bottom: 4px;
      width: 130px;
      height: 30px;
      font-size: 10px;
    }
    #searchField{
      width: 100px;
      font-size: 11px;
      height: 25px;
    }
    #backButton{
      width: 130px;
      height: 25px;
      
      font-size: 12px;
    }
}